import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import validate from "./validate";
import { connect } from "react-redux";
import submitForm from "../actions";
import Email from "react-icons/lib/fa/envelope-o";

class ContactMe extends Component {
  renderField = field => {
    const { meta: { touched, error } } = field;
    const classNameDanger = `${touched && error ? "input is-danger" : "input"}`;
    return (
      <div className="field">
        <label htmlFor={field.name} className="label">
          {field.label}
        </label>
        <p className="control has-icons-left">
          <input
            {...field.input}
            id={field.name}
            className={classNameDanger}
            type="input"
            name={field.whereToSend}
            placeholder={field.label}
          />
          <span className="has-text-danger">{touched ? error : ""}</span>
          <span className="icon is-small is-left">
            <Email />
          </span>
        </p>
      </div>
    );
  };

  renderTextArea = field => {
    const { meta: { touched, error } } = field;
    const classNameDanger = `${touched && error
      ? "is-danger textarea"
      : "textarea"}`;
    return (
      <div className="field">
        <label htmlFor="msg" className="label">
          Message
        </label>
        <div className="control">
          <textarea
            {...field.input}
            id="msg"
            className={classNameDanger}
            name="message"
            placeholder="Message"
          />
          <span className="has-text-danger">{touched ? error : ""}</span>
        </div>
      </div>
    );
  };

  onSubmit = (values, dispatch) => {
    this.props.submitForm(values);
    this.props.reset("submitForm");
    console.info(values);
    setTimeout(function() {
      window.alert("Great! I'll be in touch");
    }, 1500);
  };

  render() {
    const { handleSubmit } = this.props;
    return (
      <div className="tile is-parent animated fadeIn slowestFadeIn">
        <div className="tile is-child box">
          <fieldset>
            <legend>Contact Me</legend>
            <form onSubmit={handleSubmit(this.onSubmit.bind(this))}>
              <Field
                component={this.renderField}
                label="Email"
                name="email"
                whereToSend="_replyTo"
              />
              <Field
                component={this.renderField}
                label="Subject"
                name="subject"
                whereToSend="_subject"
              />
              <Field name="msg" component={this.renderTextArea} />
              <button
                className="button is-primary submit"
                type="submit"
                value="Send"
              >
                Submit
              </button>
            </form>
          </fieldset>
        </div>
      </div>
    );
  }
}

export default reduxForm({
  form: "ContactForm",
  validate
})(connect(null, { submitForm })(ContactMe));



