import React, { Component } from "react";
import CenterMePic from "./images/portrait.jpg";

import Css3Icon from "./images/css3.png";
import GraphQLIcon from "./images/GraphQL_Logo.svg.png";
import Html5Icon from "./images/HTML5_Logo_512.png";
import JavaScriptIcon from "./images/javascript_logo.png";
import SQLIcon from "./images/mysql.png";
import MongoDBIcon from "./images/mongodb.png";
import PhpIcon from "./images/php.svg";
import ReactIcon from "./images/reactIcon.png";
import SassIcon from "./images/sass.svg";
import VueIcon from "./images/vuejs.png";

export default class Body extends Component {
  render() {
    return (
      <div className="animated fadeIn">
        <div className="tile is-ancestor">
          <div className="tile is-parent">
            <div className="tile is-child box animated fadeInUp slowerFadeIn">
              <div>
                <figure className="has-text-centered">
                  <img
                    src={CenterMePic}
                    style={CenterPictureStyle}
                    alt="Johnathon"
                  />
                </figure>
              </div>
              <br />
              <p className="quote has-text-centered">
                <q>Luck is what happens when preparation meets opportunity.</q>
                <span> -Seneca</span>
              </p>
            </div>
          </div>
          <div className="tile is-4 slowestFadeIn is-vertical is-parent animated fadeInUp">
            <div className="tile is-child box">
              <p>
                I'm Johnathon and I currently live in Oregon. My mission is to
                provide great websites for awesome clients. I develop creative
                solutions for back-end and front-end problems. I enjoy building
                all kinds of websites, whether that be full blown web
                applications or static websites just providing information. The
                websites I provide are responsive and secure. I use a range of
                technologies to make your web presence happen. From HTML5/CSS3
                and JavaScript, to PHP and nodejs.
              </p>
            </div>
          </div>
        </div>
        <div className="tile is-parent slowestFadeIn animated fadeInDown">
          <div className="tile is-child box">
            <h2 className="has-text-centered has-text-grey-darker is-size-6">
              Here are some of the technologies I am comfortable with.
            </h2>
            <br />
            <div style={techIconDivStyle}>
              <a
                href="https://developer.mozilla.org/en-US/docs/Web/Guide/HTML/HTML5"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  className="techIcons slowPulse animated infinite pulse"
                  src={Html5Icon}
                  alt="HTML5 Icon"
                />
              </a>
              <a
                href="https://developer.mozilla.org/en-US/docs/Web/CSS/CSS3"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  className="techIcons slowPulse animated infinite pulse"
                  src={Css3Icon}
                  alt="CSS3 Icon"
                />
              </a>
              <a
                href="http://sass-lang.com/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={SassIcon}
                  alt="SASS Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
              <a
                href="https://developer.mozilla.org/en-US/docs/Web/JavaScript"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={JavaScriptIcon}
                  alt="JavaScript Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
              <a
                href="https://vuejs.org/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={VueIcon}
                  alt="Vuejs Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
              <a
                href="https://facebook.github.io/react/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={ReactIcon}
                  alt="Reactjs Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
              <a
                href="http://graphql.org/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={GraphQLIcon}
                  alt="GraphQL Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
              <a
                href="https://www.mongodb.com/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={MongoDBIcon}
                  alt="MongoDB Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
              <a
                href="https://www.mysql.com/"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={SQLIcon}
                  alt="mySQL Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
              <a
                href="http://php.net/manual/en/intro-whatis.php"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img
                  src={PhpIcon}
                  alt="PHP Icon"
                  className="techIcons slowPulse animated infinite pulse"
                />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const techIconDivStyle = {
  alignItems: "center",
  display: "flex",
  justifyContent: "space-around"
};

const CenterPictureStyle = {
  borderRadius: "8px",
  display: "inline-block",
  height: "auto",
  margin: "0 auto",
  MaxWidth: "100%"
};



